<template>
  <div class="top">
    <div :key="magicKey">
      <div
        v-for="(item,i) in scaleList"
        :key="i"
        class="box1"
      >
        <div class="bigbox">
          <p class="scalebox">{{ item.scaleName }}</p>
          <span class="btn_change">
          </span>
        </div>
        <div
          v-if="item['statu']"
          class="pro"
        >
          <ul
            v-for="(childItem,childIndex) in item.childList"
            :key="childIndex"
          >
            <li
              class="li"
              @click="jumpto(item,childItem)"
            >
              <p>{{ childItem.scaleName }}</p>
              <p style="color:#f60;">
                今日房价：&yen;{{ childItem.realDailyAmount }}/晚
                <span style="color:#2c3e50;">
                  <img
                    class="btn_go"
                    src="../../assets/right.png"
                    alt
                  />
                </span>
              </p>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { GetBusinessRoomscaleByhotel, GetBusinessVirtualRoomscaleDaypriceByroomscale } from '@/api/api_business_hotel_and_scale';
export default {
    name: 'Price',
    data() {
        return {
            scaleList: [],
            magicKey: 0,
            dataInfo: []
        };
    },
    created() {
        this.$store.commit('setHeader', '房价管理');
    },
    mounted() {
        this.HotelStateListGet();
    },
    methods: {
        HotelStateListGet() {
            GetBusinessRoomscaleByhotel(this.$route.query.hotelId).done((res) => {
                this.scaleList = res.data;
                for (var i = 0; i < this.scaleList.length; i++) {
                    this.btn_change(this.scaleList[i]);
                }
            });
        },
        async btn_change(item, i) {
            this.bookMonth = item.bookMonth;
            if (!item.statu) {
                await this.getinfo(item);
                item['childList'] = JSON.parse(JSON.stringify(this.all));
                item['statu'] = true;
            } else {
                item['statu'] = false;
            }
            this.magicKey++;
        },
        async getinfo(item) {
            await GetBusinessVirtualRoomscaleDaypriceByroomscale(item.id).done((res) => {
                this.dataInfo = res.data;
                this.all = this.dataInfo;
            });
        },
        jumpto(item, childItem) {
            localStorage.removeItem('ota_scaleList');
            this.$router.push({
                path: '/pricedetail',
                query: {
                    scaleName: childItem.scaleName,
                    pro_id: childItem.id,
                    scale_id: item.id,
                    bookMonth: item.bookMonth
                }
            });
        }
    }
};
</script>
<style scoped>
.top {
    position: fixed;
    top: 50px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
    padding: 20px 0;
}
.bigbox {
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    line-height: 34px;
    background: #fff;
    border-bottom: 1px solid #f2f2f2;
    align-items: center;
}
.scalebox {
    width: 70%;
    font-weight: bold;
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.btn_change {
    color: #989898;
    display: flex;
    align-items: center;
    padding: 10px 0;
}
.statu {
    display: inline-block;
    width: 20px;
    height: 20px;
}
.li {
    display: flex;
    font-size: 14px;
    justify-content: space-between;
    padding: 10px 20px 10px 36px;
    align-items: center;
    border-bottom: 1px solid #f2f2f2;
}
.btn_go {
    display: inline-block;
    width: 20px;
    height: 20px;
    vertical-align: bottom;
}
.box {
    font-weight: bold;
    font-size: 14px;
    display: flex;
    padding: 5px 30px;
    background: #fff;
    line-height: 30px;
}
ul,
li {
    padding: 0;
    margin: 0;
}
</style>



